import { useCompoundBody } from '@react-three/cannon';
import { WALL_LENGTH, NUM_COLUMNS, NUM_ROWS, WALL_HEIGHT } from '../constants';
import { getBoxProperties } from '../utils/getBoxProperties';
import { Ground } from './Ground';
import { Wall } from './Wall';

const PLANE_POS = [0, WALL_HEIGHT, 15];
const ROTATION = [-Math.PI / 2, Math.PI, 0];

export function MazeCompound({ walls, boardRotation }) {
  const width = NUM_COLUMNS * WALL_LENGTH;
  const height = NUM_ROWS * WALL_LENGTH;

  const shapes = [
    ...walls.map((coordinates) => {
      const { position, size } = getBoxProperties(coordinates);

      return {
        type: 'Box',
        position,
        args: size,
      };
    }),
    {
      type: 'Plane',
      rotation: [-Math.PI / 2, 0, 0],
      position: [width / 2, 0, height / 2],
    },
  ];

  const [ref] = useCompoundBody(
    () => ({
      mass: 0,
      position: [width / 2, 0, height / 2],
      rotation: [0, 0, 0],
      shapes,
    }),
    null,
    []
  );

  return (
    <group ref={ref}>
      {walls.map((coordinates) => {
        return <Wall coordinates={coordinates} />;
      })}
      <mesh castShadow receiveShadow position={PLANE_POS} rotation={ROTATION}>
        <planeGeometry args={[width, height]} />
        <meshBasicMaterial />
      </mesh>
      <Ground />
    </group>
  );
}
