import useMouse from '@react-hook/mouse-position';
import { useScreenOrientation } from './useScreenOrientation';

export const useBoardRotation = (ref) => {
  // 1. check if on phone, and use accelerometer values if so

  const orientation = useScreenOrientation();

  const mouse = useMouse(ref, {
    enterDelay: 100,
    leaveDelay: 100,
  });

  const isMobile = typeof orientation.alpha === 'number';

  if (isMobile) {
    return normalizeOrientation(orientation);
  }
  return normalizeMouse(mouse);
};

function normalizeMouse(mouse) {
  if (mouse.x === null) {
    return { x: 0, y: 0 };
  }

  const xRotation = (mouse.x - window.innerWidth / 2) * -1;
  const yRotation = mouse.y - window.innerHeight / 2;
  return {
    x: xRotation / 1500,
    y: yRotation / 1500,
  };
}

function normalizeOrientation(orientation) {
  if (window.innerWidth < window.innerHeight) {
    const xRotation = orientation.gamma * -1;
    const yRotation = orientation.beta;
    return {
      x: xRotation / 100,
      y: yRotation / 100,
    };
  } else {
    const xRotation = orientation.beta * -1;
    const yRotation = orientation.gamma * -1;
    return {
      x: xRotation / 100,
      y: yRotation / 100,
    };
  }
}
