import { useSphere } from '@react-three/cannon';
import { useLoader } from '@react-three/fiber';
import { TextureLoader } from 'three';
import { WALL_LENGTH, METAL_TEXTURE } from '../constants';

export function Ball() {
  const DROP_HEIGHT = 15;

  const metalTexture = useLoader(TextureLoader, METAL_TEXTURE);
  const [ref] = useSphere(() => ({
    mass: 10,
    position: [WALL_LENGTH / 2, DROP_HEIGHT, WALL_LENGTH / 2],
  }));

  return (
    <mesh castShadow receiveShadow ref={ref}>
      <sphereGeometry args={[0.85, 16, 16]} />
      <meshPhysicalMaterial
        metalness={0.3}
        roughness={0.4}
        reflectivity={1}
        map={metalTexture}
      />
    </mesh>
  );
}
