import { Canvas } from '@react-three/fiber';
import { NoToneMapping } from 'three';
import Scene from './components/Scene';
import { useRef, useState } from 'react';
import { useBoardRotation } from './utils/useBoardRotation';
import { useTimeout } from 'usehooks-ts';

function App() {
  const ref = useRef(null);
  const [boardLocked, setBoardLocked] = useState(true);
  const boardRotation = useBoardRotation(ref);
  const unlock = () => setBoardLocked(false);
  const [isFullScreen, setIsFullScreen] = useState(false);

  const fullScreen = () => {
    setIsFullScreen(true);
    document.body
      .requestFullscreen()
      .then(() => window.screen.orientation.lock('natural'));
  };

  const exitFullScreen = () => {
    setIsFullScreen(false);
    document.exitFullscreen();
  };

  useTimeout(unlock, 1000);

  return (
    <div className="App" ref={ref}>
      <div id="canvas-container" style={{ height: '100vh' }}>
        <Canvas
          onCreated={({ gl }) => {
            gl.toneMapping = NoToneMapping;
          }}
        >
          <Scene boardRotation={boardLocked ? { x: 0, y: 0 } : boardRotation} />
        </Canvas>

        {!isFullScreen && (
          <button
            onClick={fullScreen}
            style={{ position: 'absolute', bottom: '10px', right: '10px' }}
          >
            Full screen
          </button>
        )}

        {isFullScreen && (
          <button
            onClick={exitFullScreen}
            style={{ position: 'absolute', bottom: '10px', right: '10px' }}
          >
            Exit full screen
          </button>
        )}
      </div>
    </div>
  );
}

export default App;
