import { Maze } from '../mazeGenerator';
import {
  BOARD_ROTATION_SENSITIVITY,
  HELPER_CAMERA,
  NUM_COLUMNS,
  NUM_ROWS,
  ORBIT_CAMERA,
  WALL_LENGTH,
} from '../constants';
import { Ball } from './Ball';
import { Physics } from '@react-three/cannon';
import { MazeCompound } from './MazeCompound';
import { MainCamera } from './MainCamera';
import { OrbitCamera } from './OrbitCamera';
import { HelperCamera } from './HelperCamera';

let newMaze = new Maze(WALL_LENGTH, NUM_COLUMNS, NUM_ROWS);
newMaze.setup();
console.log('newMaze.lastCell', newMaze.lastCell);

function Scene({ boardRotation }) {
  const gravity = [boardRotation.x * -150, -350, boardRotation.y * 150];
  const cameraRotation = [
    -Math.PI / 2 - (boardRotation.y * BOARD_ROTATION_SENSITIVITY) / 100,
    (boardRotation.x * BOARD_ROTATION_SENSITIVITY) / 100,
    0,
  ];
  const cameraPosition = [
    (NUM_COLUMNS * WALL_LENGTH) / 2 +
      boardRotation.x * BOARD_ROTATION_SENSITIVITY,
    100,
    (NUM_ROWS * WALL_LENGTH) / 2 +
      boardRotation.y * -BOARD_ROTATION_SENSITIVITY,
  ];
  return (
    <>
      {HELPER_CAMERA && <HelperCamera />}
      <MainCamera rotation={cameraRotation} position={cameraPosition} />
      {ORBIT_CAMERA && <OrbitCamera />}

      <ambientLight intensity={0.3} />
      <directionalLight color="white" position={[0, 0, 20]} />

      <Physics gravity={gravity}>
        <MazeCompound walls={newMaze.walls} boardRotation={boardRotation} />
        <Ball />
      </Physics>
    </>
  );
}

export default Scene;
