import { pickRandom } from './utils';
import filter from 'ramda.filter';
import { OPPOSITES } from './constants';

export class Maze {
  constructor(size, rows, columns) {
    this.size = size;
    this.rows = rows;
    this.columns = columns;
    this.grid = []; // 2d array of cells
    this.stack = [];
    this.walls = []; // walls in format ready for 3d renderer
    this.lastCell = null;
  }

  setup() {
    for (let r = 0; r < this.rows; r++) {
      let row = [];
      for (let c = 0; c < this.columns; c++) {
        let cell = new Cell(r, c, this.grid, this.size);
        row.push(cell);
      }
      this.grid.push(row);
    }

    const startingCell = this.grid[0][0];

    startingCell.visited = true;
    this.stack.push(startingCell);
    this.createPath();
    this.generateWalls();
  }

  // create the path by removing walls
  createPath() {
    if (this.stack.length > 0) {
      const currentCell = this.stack.pop();
      this.lastCell = { x: currentCell.x, y: currentCell.y };

      const unvisitedNeighbors = this.findUnvisitedNeighbors(
        currentCell.x,
        currentCell.y
      );

      const unvisitedDirections = Object.keys(unvisitedNeighbors);

      const hasUnvisitedNeighbors = Boolean(unvisitedDirections.length);

      if (hasUnvisitedNeighbors) {
        this.stack.push(currentCell);
        const randomDirection = pickRandom(unvisitedDirections);
        const randomNeighbor = unvisitedNeighbors[randomDirection];

        // remove walls
        currentCell.hasWall[randomDirection] = false;
        const oppositeDirection = OPPOSITES[randomDirection];
        randomNeighbor.hasWall[oppositeDirection] = false;

        randomNeighbor.visited = true;
        this.stack.push(randomNeighbor);
      }

      this.createPath();
    }
  }

  findUnvisitedNeighbors(x, y) {
    return filter((cell) => cell?.visited === false, {
      top: this.grid[x][y + 1],
      right: this.grid[x + 1]?.[y],
      bottom: this.grid[x][y - 1],
      left: this.grid[x - 1]?.[y],
    });
  }

  getXPos(val) {
    return val * this.size;
  }

  getYPos(val) {
    return val * this.size;
  }

  generateWalls() {
    this.walls = [];
    for (const row of this.grid) {
      for (const cell of row) {
        if (cell.hasWall.top) {
          this.walls.push([
            [this.getXPos(cell.x), this.getYPos(cell.y) + this.size],
            [
              this.getXPos(cell.x) + this.size,
              this.getYPos(cell.y) + this.size,
            ],
          ]);
        }

        if (cell.hasWall.right) {
          this.walls.push([
            [this.getXPos(cell.x) + this.size, this.getYPos(cell.y)],
            [
              this.getXPos(cell.x) + this.size,
              this.getYPos(cell.y) + this.size,
            ],
          ]);
        }
        if (cell.hasWall.bottom) {
          const belowCell = this.grid[cell.x][cell.y - 1];
          if (!belowCell?.hasWall.top) {
            this.walls.push([
              [this.getXPos(cell.x), this.getYPos(cell.y)],
              [this.getXPos(cell.x) + this.size, this.getYPos(cell.y)],
            ]);
          }
        }
        if (cell.hasWall.left) {
          const leftCell = this.grid[cell.x - 1]?.[cell.y];
          if (!leftCell?.hasWall.left) {
            this.walls.push([
              [this.getXPos(cell.x), this.getYPos(cell.y)],
              [this.getXPos(cell.x), this.getYPos(cell.y) + this.size],
            ]);
          }
        }
      }
    }
  }
}

class Cell {
  constructor(x, y, grid) {
    this.x = x;
    this.y = y;
    this.parentGrid = grid;
    this.visited = false;
    this.hasWall = {
      top: true,
      right: true,
      bottom: true,
      left: true,
    };
  }
}
