import { MAZE_HEIGHT, MAZE_WIDTH, WALL_HEIGHT } from '../constants';

export function getBoxProperties(coordinates) {
  const [[x1, y1], [x2, y2]] = coordinates;

  const xDiff = x2 - x1;
  const yDiff = y2 - y1;

  const isHorizontal = y1 === y2;
  // if its horizontal use xDiff + 0.1

  const yAdjustment = WALL_HEIGHT / 2;

  if (isHorizontal) {
    return {
      position: [compensateX(x1 + xDiff / 2), yAdjustment, compensateY(y1)],
      size: [xDiff, WALL_HEIGHT, 0.1],
    };
  } else {
    return {
      position: [compensateX(x1), yAdjustment, compensateY(y1 + yDiff / 2)],
      size: [0.1, WALL_HEIGHT, yDiff],
    };
  }
}

// Shift the x value half the maze value back (originally in center)
function compensateX(val) {
  return val - MAZE_WIDTH / 2;
}

function compensateY(val) {
  return val - MAZE_HEIGHT / 2;
}
