import { useLoader } from '@react-three/fiber';
import { TextureLoader } from 'three';
import { WOOD_TEXTURE } from '../constants';
import { getBoxProperties } from '../utils/getBoxProperties';

export function Wall({ coordinates }) {
  const { position, size } = getBoxProperties(coordinates);

  const colorMap = useLoader(TextureLoader, WOOD_TEXTURE);

  return (
    <mesh position={position}>
      <boxGeometry args={size} />
      <meshStandardMaterial map={colorMap} />
    </mesh>
  );
}
